html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
}

a {
  text-decoration: none;
  color: #fff;
  font-size: 2.8rem;
  text-transform: capitalize;
  margin: 0;
}

.correo {
  text-transform: lowercase;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

h1,
h2,
h3 {
  margin: 0 0 5rem 0;
  text-align: center;
}

h2 {
  font-size: 2.8rem;
}
@media (min-width: 768px) {
  h2 {
    font-size: 3.5rem;
  }
}

h3 {
  font-size: 2.5rem;
}

p {
  font-size: 2rem;
}

.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

.boton {
  display: block;
  font-size: 2.5rem;
  border: 2px solid #fff;
  border-radius: 0.5rem;
  padding: 1rem 3rem;
  align-items: center;
  text-align: center;
}

.boton_naranja {
  color: #ff6c00;
  border: 2px solid #ff6c00;
  font-weight: bold;
}

/*Frames Animaciones */
@keyframes moverIzquierda {
  0% {
    opacity: 0;
    transform: translate(-100px);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@keyframes moverDerecha {
  0% {
    opacity: 0;
    transform: translate(100px);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@keyframes moverArriba {
  0% {
    opacity: 0;
    transform: translateY(50px);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@keyframes moverAbajo {
  0% {
    opacity: 0;
    transform: translateY(-50px);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
.header {
  background: #f12711;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #f5af19, #f12711);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #f5af19, #f12711);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  height: auto;
  padding: 2rem;
}

.navegacion {
  display: flex;
  justify-content: space-between;
  font-weight: bold;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0rem;
  z-index: 100;
  padding-right: 5rem;
  background: #f12711;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #f5af19, #f12711);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #f5af19, #f12711);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.header_menu {
  display: flex;
  z-index: 10;
  align-items: center;
  gap: 1rem;
  padding: 2rem 0;
  font-size: 2.5rem;
  padding-left: 3rem;
  justify-content: flex-start;
  animation: moverIzquierda 1s ease-in;
}

.navegacion__enlaces {
  background: #0575E6;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #021B79, #0575E6);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #021B79, #0575E6);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  position: fixed;
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100%;
  top: 0%;
  left: 0%;
  padding-top: 6rem;
  transition: all 1s ease;
  width: 100%;
}

.navegacion_enlace {
  display: inline-block;
  font-size: 2.5rem;
  padding: 1.5rem 0;
  width: 100%;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion_enlace {
    text-align: left;
    padding: 2rem 0rem 2rem 15rem;
  }
  .navegacion_enlace:hover {
    text-decoration: underline;
  }
}

.uno {
  -webkit-clip-path: circle(0% at 0 0);
  clip-path: circle(0% at 0 0);
}

.dos {
  -webkit-clip-path: circle(73% at 0 0);
  clip-path: circle(78% at 0 0);
}
@media (min-width: 992px) {
  .dos {
    -webkit-clip-path: circle(73% at 0 0);
    clip-path: circle(58% at 0 0);
  }
}

.contenido__imagen {
  margin-top: 1.5rem;
  animation: moverDerecha 1s ease-in;
}
@media (min-width: 768px) {
  .contenido__imagen {
    margin-top: 0rem;
  }
}
@media (min-width: 992px) {
  .contenido__imagen {
    animation: moverArriba 1s ease-in;
    margin: 0%;
  }
}

.header__contenido {
  display: flex;
  flex-direction: column-reverse;
  margin-top: 3rem;
}

.contenido__textos {
  text-align: center;
  color: #fff;
  text-transform: capitalize;
}
.contenido__textos h1 {
  font-size: 3.5rem;
  animation: moverIzquierda 1s ease-in;
}
.contenido__textos h2 {
  font-size: 2.3rem;
  margin-bottom: 1;
  animation: moverDerecha 1s ease-in;
}

.contenido_contacto {
  display: inline-block;
  animation: moverArriba 1s ease-in;
  margin-bottom: 2.5rem;
}

@media (min-width: 992px) {
  .header__contenido {
    display: grid;
    margin-top: 7rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0rem;
  }
  .header__contenido h1 {
    font-size: 5.5rem;
    line-height: 1.5;
    text-align: left;
  }
  .header__contenido h2 {
    font-size: 3.6rem;
    line-height: 1.2;
    margin-top: 2.3rem;
    text-align: left;
  }

  .contenido__textos {
    width: 100%;
    text-align: left;
  }
}
.wave {
  height: 10rem;
  width: 100%;
  background: #f12711;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #f5af19, #f12711);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #f5af19, #f12711);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.wave path {
  fill: #fff;
}

.presentacion {
  margin-top: 3rem;
  background-color: #e1e1e1;
  padding: 3rem 1rem 1rem 1rem;
  text-align: center;
}
.presentacion h3 {
  padding: 1.3rem 0 2.5rem 0;
  margin-bottom: 0;
  border-bottom: 1px solid #bdbdbd;
  font-size: 2.3rem;
  font-weight: normal;
  font-weight: bold;
}
@media (min-width: 768px) {
  .presentacion h3 {
    border-bottom: 0;
    border-right: 1px solid #bdbdbd;
    text-align: left;
    font-size: 3rem;
    padding: 3rem;
    margin-top: 1em;
  }
}
.presentacion p {
  font-size: 2rem;
