  margin-top: 0;
}
@media (min-width: 768px) {
  .presentacion p {
    text-align: left;
    line-height: 1.2;
    margin-top: 3rem;
  }
}
.presentacion p span {
  font-weight: bold;
}

@media (min-width: 768px) {
  .presentacion__contenido {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.servicios {
  margin-top: 6rem;
}
.servicios h2 {
  color: #ff6c00;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .servicios h2 {
    margin-bottom: 1.5rem;
  }
}

@media (min-width: 768px) {
  .contenedor__Servicios {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;
  }
}

.servicio {
  background-color: #e1e1e1;
  padding: 3rem;
  margin-top: 3rem;
  border-radius: 1rem;
}
.servicio h3 {
  margin-bottom: 0;
}
.servicio h3:before {
  content: "";
  display: block;
  width: 12rem;
  height: 12rem;
  background-size: 12rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 1rem auto 2rem auto;
}

.paginas h3:before {
  background-image: url(../img/pagina.webp);
}

.ecomerce h3:before {
  background-image: url(../img/ecomerce.webp);
}

.marketing h3:before {
  background-image: url(../img/marketing.webp);
}

@media (min-width: 768px) {
  .s_animacion__Abajo h3,
.s_animacion__Abajo h3:before {
    animation: moverAbajo 0.8s ease-in;
  }
}

.beneficios {
  background-color: #1a2e35;
  margin-top: 6rem;
  padding: 5rem 0 3rem 0;
}

.beneficios_encabezado h2 {
  color: #fff;
  margin-bottom: 1.5rem;
